ul {
  list-style-type: none; }

body {
  font-family: -apple-system, system-ui, "Segoe UI", Roboto,  "Helvetica Neue", Arial, sans-serif; }

.blue {
  color: #0184bc; }

.violet {
  color: #a626a4; }

.gold {
  color: #986801; }

.sml-txt {
  font-size: 0.8rem; }

.exp {
  padding: 30px 30px 0 30px;
  grid-area: exp; }

.ovvw {
  max-width: 618px;
  padding: 30px 30px 0 30px;
  grid-area: ovvw; }

.edu {
  padding: 30px 30px 0 30px;
  grid-area: edu; }

.header {
  padding: 30px 30px 0 30px;
  grid-area: header; }

.about {
  padding-bottom: 30px;
  display: grid;
  grid-template-areas: "header header" "ovvw ovvw" "exp exp" "edu edu"; }

@media screen and (min-width: 1300px) {
  .about {
    padding: 30px;
    display: grid;
    grid-template-areas: "header header header header" "ovvw ovvw ovvw ovvw" "exp exp edu edu"; } }



